import { Company, Job, Wrapper } from "./styles";

interface IRoadmapTitle {
  $jobName: string;
  $companyName: string;
}

function RoadmapTitle({ $jobName, $companyName }: IRoadmapTitle) {
  //TODO -    ,  
  return (
    <Wrapper>
      <Job>{$jobName}</Job>
      <Company>{$companyName}</Company>
    </Wrapper>
  );
}

export default RoadmapTitle;
